import {Base64} from 'js-base64';
import Octokit from "@octokit/rest/index"
import VueCookies from 'vue-cookies'
import config from './github.config'

const github = {
    owner: config.owner,
    repo: config.defaultRepo,
    defaultRepo: config.defaultRepo,
    personalRepo: config.personalRepo,
    publicToken: config.publicToken,
    oauthAppsClientId: config.oauthAppsClientId,
    githubAppsClientId: config.githubAppsClientId,
    oauthUrl: config.oauthUrl,
    client: null,
    defaultClient: new Octokit({
        auth: "token " + config.publicToken //public only
    })
};

function prepare() {
    if (!github.client) {
        let token = VueCookies.get("access_token");
        if (token) {
            github.client = new Octokit({
                auth: 'token ' + token
            });
        }
    }

    return new Promise((resolve, reject) => {
        if (github.client) {
            resolve(github.client);
        } else {
            resolve(github.defaultClient);
        }
    });
}


export default {
    config: github,
    contents(path, format) {
        return prepare().then(client =>
            client.repos.getContents({
                "owner": github.owner,
                "repo": github.repo,
                "path": path,
                "headers": {
                    "If-None-Match": ""
                },
                "mediaType": {
                    "format": format || "html" // object raw
                }
            }).then(promise => {
                let contentType = promise.headers['content-type'] || "";

                if(contentType.indexOf('application/vnd.github.v3.raw') !== -1
                    || contentType.indexOf('application/vnd.github.v3.html') !== -1){
                    promise.type = "file"
                }

                if(contentType.indexOf("application/json") !== -1){
                    if(Array.isArray(promise.data)){
                        promise.type = "dir";
                    }
                    else{
                        promise.type = promise.data.type;
                    }
                }

                return promise;
            })
        );
    },
    remove(item) {
        return prepare().then(client => {
            return client.repos.deleteFile({
                "owner": github.owner,
                "repo": github.repo,
                "path": item.path,
                "message": "octokit: [master] page deleted",
                "sha": item.sha
            });
        });
    },
    commit(item) {
        return prepare().then(client => {
            return client.repos.createOrUpdateFile({
                "owner": github.owner,
                "repo": github.repo,
                "path": item.path,
                "message": "octokit: [master] page modified",
                "content": Base64.encode(item.text),
                "sha": item.sha
            });
        });
    },
    user() {
        return prepare().then(client => {
            return client.users.getAuthenticated();
        })
    },

    search(query){
        return prepare().then(client => {
            return client.search.code({
                "q": query.q + "+repo:" + github.owner + "/" + github.repo,
                "page": query.page,
                "per_page" : query.perPage,
                "mediaType": {
                    'format': 'text-match+json'
                }
            })
        })

    },

    /**
     * encrypto 加密程序
     * @param {String} str 待加密字符串
     * @return {String} 加密后的字符串
     */
    encode(str) {
        return Base64.encodeURI(str);
    },

    /**
     * decrypto 解密程序
     * @param {String} str 待加密字符串
     * @return {String} 加密后的字符串
     */
    decode(str) {
        return Base64.decode(str);
    },

    parentDir(path) {
        return path.replace(/\/?([^/]+\/?)?$/, "");
    },

    prefix(){
        return this.config.repo === this.config.defaultRepo && '/t/' || '/p/';
    },

    repo(prefix){
        return prefix === 't' && this.config.defaultRepo || this.config.personalRepo;
    }


}


