<template>
    <div class="header">

        <div class="container">
            <span class="logo">
                MarkdownBook <span class="logo iconfont icon-jiesuo"></span>
            </span>
            <nav class="main-nav">
                <ul class="nav-list">
                    <li class="nav-item search">
                        <form role="search" class="nav-form">
                            <input type="search" maxlength="32" placeholder="关键字" class="nav-input"
                                   v-model="keyword">
                            <a class="nav-icon iconfont icon-sousuo" v-on:click="search"></a>
                        </form>
                    </li>
                    <template v-if="login === github.owner">
                        <template v-if="$route.name === 'editor'">
                            <li class="nav-item search">
                                <form role="search" class="nav-form">
                                    <input v-bind:readonly="file.sha" type="search" maxlength="32" placeholder="文件名称" class="nav-input"
                                           v-model="path">.md
                                    <a v-bind:class="{ disabled: !!processing, blink: processing === 'save'}"
                                       v-on:click="saveFile" class="nav-icon iconfont icon-baocun"> </a>
                                    <a v-bind:class="{ disabled: !!processing }"
                                       v-on:click="discardFile" class="nav-icon iconfont icon-chexiao"></a>
                                    <a v-if="file.sha" v-bind:class="{ disabled: !!processing, blink: processing === 'remove'}"
                                       v-on:click="removeFile" class="nav-icon iconfont icon-shanchu"></a>
                                </form>
                            </li>

                        </template>

                        <template v-else>
                            <li class="nav-item"><a v-bind:class="{ disabled: !!processing }"
                                   v-on:click="newFile" class="iconfont icon-jiahao"></a></li>

                            <template v-if="file.sha">
                                <li class="nav-item"><a v-bind:class="{ disabled: !!processing }"
                                       v-on:click="editFile" class="iconfont icon-bianjibiaoge"></a>
                                </li>
                            </template>
                        </template>
                    </template>
                    <li class="nav-item">
                        <img v-if="login" class="avatar" v-bind:title="'@' + login + ' 登出'" v-bind:alt="'@' + login"
                             v-bind:src="avatar" v-on:click="logout()">
                        <a v-else-if="!token"  @click.exact="goLogin(false)"
                           @click.ctrl.exact="goLogin(true)">登录</a>
                    </li>
                </ul>
            </nav>
        </div>
    </div>
</template>

<script>
    import GithubApi from '../github';
    import {mapMutations, mapState} from 'vuex'

    export default {
        name: "Header",
        computed: {
            ...mapState([
                'file',
                'dir'
            ])
        },
        data() {
            return {
                processing: false,
                login: null,
                avatar: null,
                keyword: null,
                path: null,
                token: null,
                github: GithubApi.config
            }
        },
        mounted() {
            this.initial();
        },
        watch: {
            "$route"(from, to) {
                this.initial();
            },
            path(newValue, prevValue){
                if(!/^\w*(\w+\/?){0,2}\w*$/.test(newValue)){
                    this.$nextTick(() => {
                        this.path = prevValue;
                    });
                }
            }
        },
        methods: {
            ...mapMutations([
                "receiveFile",
                "receiveDir"
            ]),

            initial() {
                if(this.$route.name === 'editor') {
                    //编辑文件状态
                    if (this.$route.params.sha) {
                        this.file.sha = this.$route.params.sha;
                        this.file.path = this.$route.params.path || '';
                        this.path = this.file.path.replace(/\.md$/, '');
                    } else{
                        //新建文件状态
                        this.path = this.dir && (this.dir + "/newfile") || "newfile";
                        this.receiveDir(this.$route.params.mkdir || "");
                    }
                }

                if (this.$route.params.repo) {
                    GithubApi.config.repo = GithubApi.repo(this.$route.params.repo);
                }

                let installToken = this.token;
                this.token = this.$route.query.access_token || this.$cookies.get("access_token");
                this.keyword = this.$route.params.keyword;

                //token change
                if (installToken !== this.token) {
                    this.updateUser(this.token);
                }

            },

            search() {
                return this.$router.push({
                    name: "search",
                    params: {
                        keyword: this.keyword
                    }
                });
            },
            updateUser(token) {
                if (token) {
                    if (this.$cookies.get("access_token") !== token) {
                        this.$cookies.set("access_token", token, "7d");
                    }
                    GithubApi.user().then(promise => {
                        this.login = promise.data.login;
                        this.avatar = promise.data.avatar_url + "&s=24";
                    })
                } else {
                    this.token = null;
                    this.login = null;
                    this.avatar = null;
                }
            },
            goHome(callback) {
                return this.$router.push(GithubApi.prefix() + (this.file.path || this.dir));
            },
            goLogin(me) {
                return this.$router.push({
                    name: "login",
                    query: {
                        me: me,
                        redirectUrl: window.location.href
                    }
                });
            },
            logout() {
                this.$cookies.remove("access_token"); //清除sessionStorage
                GithubApi.config.client = null; //清除全局变量
                this.updateUser(null);
            },
            newFile() {
                this.receiveFile({}); //清楚狀態
                return this.$router.push("/add/" + this.dir)
            },
            editFile() {
                return this.$router.push("/edit/" + this.file.sha + "/" + this.file.path)
            },
            removeFile() {
                if (this.processing) {
                    return;
                }
                this.processing = "remove";
                GithubApi.remove(this.file).then(promise => {
                    this.receiveFile({});
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            saveFile() {
                if (this.processing) {
                    return;
                }
                this.processing = "save";
                if (!this.file.sha) {
                    this.file.path = this.path + '.md';
                }
                GithubApi.commit(this.file).then(promise => {
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            discardFile() {
                this.goHome();
            }
        }
    }
</script>


